
.grid-layout {
  position: relative;
  border: 1px solid #0066CC;
  background-color: #fff;
}
.grid-box {
  position: absolute;
  width: 100px;
  height: 100px;
  border: 2px solid #4b4f56;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #e0e0e0;
}

.grid-box-title {
  color: #e0e0e0;
  background-color: #4b4f56;
  padding: 3px;
}

.grid-draggable {
  background-color: #0066CC;
  height: 16px;
  width: 16px;
  cursor: grab;
  position:absolute; 
  right: 0; 
  top: 0; 
}

.grid-resizable {
  width: 12px;
  height: 12px;
  background-color: #4b4f56;
  position:absolute; 
  right: 0; 
  bottom: 0; 
  cursor: move; 
}